:root {
  --line-thickness: .25em;
  --glass-size: 65%;
  --icon-height: 7rem;
  --icon-color: black;
  --transition-speed: .45s;
  --timing-function: cubic-bezier(.66, 1.51, .77, 1.13);
}

.search-icon {
  width: var(--icon-height);
  max-width: 20em;
  height: var(--icon-height);
  border: solid var(--line-thickness);
  transition: all var(--transition-speed) linear, border-color 0s linear var(--transition-speed);
  border-color: rgba(255, 255, 255, 0);
  border-radius: 100px;
  margin: auto;
  padding: .25em;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.search-icon-wrapper {
  width: var(--icon-height);
  height: var(--icon-height);
  transition: all 0 linear;
  border-radius: 100px;
  margin: auto 0;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  transform: rotate(-45deg);
}

.search-icon-wrapper:hover {
  cursor: pointer;
}

.search-icon-input {
  width: calc(100% - (var(--icon-height) / 2 + 1rem) );
  height: 100%;
  text-align: center;
  color: #000;
  transition: all var(--transition-speed) linear;
  background: rgba(255, 255, 255, 0);
  border: none;
  border-radius: 100px;
  outline: none;
  margin-right: 6rem;
  padding: 0 .5em;
  font-size: 2em;
  display: block;
}

.search-icon-input::-ms-input-placeholder {
  color: rgba(0, 0, 0, .75);
}

.search-icon-input::placeholder {
  color: rgba(0, 0, 0, .75);
}

.search-icon-glass {
  width: var(--glass-size);
  height: var(--glass-size);
  border: solid var(--line-thickness);
  border-color: var(--icon-color);
  transition: all var(--transition-speed) var(--timing-function) var(--transition-speed), border-color 0s linear var(--transition-speed);
  border-radius: 100px;
  margin: 0 auto;
  position: relative;
}

.search-icon-handle {
  width: var(--line-thickness);
  height: calc(100% - var(--glass-size) );
  background: var(--icon-color);
  transition: all var(--transition-speed) var(--timing-function);
  transition-delay: var(--transition-speed);
  border-radius: 0 0 100px 100px;
  margin: 0 auto;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.search-icon-handle:after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background: var(--icon-color);
  transition: all var(--transition-speed) var(--timing-function);
  transition-delay: 0s;
  display: block;
  position: absolute;
  transform: rotate(0deg);
}

.search-icon.open {
  width: calc(100vw - 1em);
  border-color: var(--icon-color);
  transition-delay: var(--transition-speed);
}

.search-icon.open .search-icon-input {
  transition-delay: var(--transition-speed);
}

.search-icon.open .search-icon-glass {
  width: 100%;
  height: 100%;
  transition: all var(--transition-speed) var(--timing-function) 0s, border-color 0s linear var(--transition-speed);
  border-color: rgba(0, 0, 0, 0);
}

.search-icon.open .search-icon-handle {
  bottom: calc(50% - (100% - var(--glass-size) ) / 2);
  border-radius: 100px;
  transition-delay: 0s;
}

.search-icon.open .search-icon-handle:after {
  transition-delay: var(--transition-speed);
  transform: rotate(90deg);
}

